.carousel-container {
  display: flex;
  position: relative;
  height: 550px;
  width: 80%;
  padding: 0 14px 0 16px;
  overflow-x: auto;
  margin: auto;
  scroll-behavior: smooth;
  /* hide scrollbar */
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
.carousel-container::-webkit-scrollbar {
  display: none;
}

.carousel-item {
  width: 20%;
  position: relative;
  max-height: 100%;
  overflow: hidden;
  /* border-radius: 32px; */
  margin-right: 8px;
  flex: 0 0 auto;
  transition: all 0.4s;
  color: var(--md-sys-color-on-surface);

  /* transition-delay: 0.3s; */
}

.carousel-item:nth-child(1) {
  width: 50%;
}
.carousel-item:nth-child(2) {
  width: 30%;
  /* ;  filter: grayscale(0.4); */
  filter: brightness(0.8);
}
.carousel-item:nth-child(3) {
  width: 20%;
  /* filter: grayscale(0.6); */
  filter: brightness(0.6);
}
.carousel-item > img {
  width: 100%;
  height: 75%;
  object-fit: cover;
  border-radius: 32px;
}

.carousel-item>p{
  display: -webkit-box;
  /* max-width: 200px; */
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

